@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
html {
    background-color: #292929;
    animation: pulse 10000ms infinite;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Roboto", sans-serif;
}

.app {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.container {
    margin-top: 60px;
    margin-bottom: 60px;
    background: #121212;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
    width: 30em;
}
.header {
    color: #e0e0e0;
    font-weight: bold;
    background: none;
    text-align: center;
    margin-top: 0.5em;
}
.btn {
    background-color: #e0e0e0;
    color: #121212;
    font-size: 1em;
    font-weight: bold;
    outline: none;
    border: none;
    padding: 1em 1em;
    border-radius: 0 10px 10px 0;
}
.btn:hover {
    background: #ec7d10;
    transition: 300ms;
}
.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}
.form {
    display: grid;
    grid-template-columns: 2fr 0.05fr;
    align-content: stretch;
    margin: 2em 0;
    width: 80%;
    border-radius: 10px;
    height: 100%;
}
.collect {
    background: #1d1d1d;
    border: none;
    outline: none;
    border-radius: 10px 0 0 10px;
    color: #e0e0e0;
    font-size: 1em;
    padding: 1em 1em;
}
.collect::placeholder {
    text-align: center;
    font-size: 1em;
}
.collect:focus {
    background: #1d1d1d4f;
    transition: 500ms;
}
.todo {
    cursor: pointer;
    background: #1d1d1d;
    text-align: center;
    padding: 10px;
    color: #e0e0e0;
    transition: 200ms ease;
    border-bottom: 0.2px #e0e0e017 solid;
    font-size: 1em;
}
.todo:hover {
    background: #e0e0e0;
    color: #1d1d1d;
}
.bttn {
    width: 100%;
    outline: none;
    border: none;
    background: #e0e0e0;
    color: #1d1d1d;
    font-weight: bold;
    font-size: 1em;
    padding: 1em;
    border-radius: 0 0 10px 10px;
    cursor: pointer;
}
.strike {
    text-decoration: line-through;
    color: #e0e0e0cc;
    background: #1d1d1d80;
    transition: 200ms ease;
}

.bttn:hover {
    background: #03dac6;
    transition: 300ms;
}
@media only screen and (max-width: 480px) {
    .container {
        width: 100vw;
        margin-top: 0;
        margin-bottom: 0px;
    }
}

@media only screen and (max-width: 300px) {
    .form {
        width: 100vw;
    }
}

@keyframes pulse {
    0% {
        background-color: #292929;
    }
    50% {
        background-color: #292929d5;
    }
    100% {
        background-color: #292929;
    }
}
